}

.skillsContainer {
    flex-direction: column;
    justify-content: center;
    background-color: #2eabc4;
}

.title {
    background-color: #8de4ef;
}

.skills {
    width: 100%;
    border: 1px solid;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
}